import React, { Component } from "react";
import logo from "./logo.svg";
import "./App.css";
import Header from "./Header";
import HeaderAdmin from "./HeaderAdmin";
import { BrowserRouter as Router , Route, Link } from "react-router-dom";
import Footer from "./Footer";
import Home from "./pages/Home";
import About from "./pages/About";
import Culture from "./pages/Culture";
import Jodhavat from "./caste/Jodhavat";
import Badaliwal from "./caste/Badaliwal";
import Badiwala from "./caste/Badiwala";
import Chatani from "./caste/Chatani";
import Dabani from "./caste/Dabani";
import Dodidar from "./caste/Dodidar";
import Khetasariya from "./caste/Khetasariya";
import Lakhawat from "./caste/Lakhawat";
import Polewall from "./caste/Polewall";
import Potaedar from "./caste/Potaedar";
import Sewag from "./caste/Sewag";
import None from "./caste/None";
import Add from "./pages/Add";
import Ancestors from "./pages/Ancestors";
import Admin from "./admin/Welcome";
import Register from "./pages/Register";
import Chart from "./pages/Chart";

import Contact from "./pages/Contact";
import $ from "jquery";
// import "../node_modules/orgchart/dist/css/jquery.orgchart.min.css";
// import "orgchart";

class App extends Component {
 
  constructor(props) {
    super(props);

    this.state = {
      isAdmin: false
    }
  }

  render() {
    return (
      <Router>
      <div>
        <div className="App">
          {this.state.isAdmin ?  <HeaderAdmin /> : <Header/>}
            <Route exact path="/" component={Home} />
            <Route exact path="/about" component={About} />
            <Route exact path="/culture" component={Culture} />
            {/* <Route exact path="/surnames" component={Surnames} />*/}
            {/* <Route exact path="/ancestors" component={Ancestors} />  */}
            {/* <Route exact path="/add" component={Add} /> */}
             <Route exact path="/contact" component={Contact} />
             <Route exact path="/jodhavat" component={Jodhavat} />
             <Route exact path="/badaliwal" component={Badaliwal} />
             <Route exact path="/Badiwala" component={Badiwala} />
             <Route exact path="/Chatani" component={Chatani} />
             <Route exact path="/Dabani" component={Dabani} />
             <Route exact path="/Dodidar" component={Dodidar} />
             <Route exact path="/Khetasariya" component={Khetasariya} />
             <Route exact path="/Lakhawat" component={Lakhawat} />
             <Route exact path="/Polewall" component={Polewall} />
             <Route exact path="/Potaedar" component={Potaedar} />
             <Route exact path="/Sewag" component={Sewag} />
             <Route exact path="/none" component={None} />
            {/*<Route exact path="/register" component={Register} />*/}
            <Route exact path="/chart" component={Chart} />
            {/* <Route exact path="/admin" component={Admin} />  */}
          <Footer />
          </div>
      </div> 
      </Router>     
    );
  }
}

export default App;



