import React, { Component } from "react";
class Add extends Component {
    render() {
        return (
            <div>
                <div class="header-banner">
                    <div class="container">
                        <h3>Name add request</h3>
                    </div>
		        </div>
				<div class="content">
				    <div class="contact">
                        <div class="container">
                            <div class="contact-grids">
                                <div class="col-md-10 col-md-offset-1 contact-right">
                                    <form>
                                        <div class="col-md-6">
                                            <input type="text" placeholder="name"/>
                                            <h5></h5>
                                            <input type="text" placeholder="surname"/>
                                            <h5></h5>
                                            <input type="text" placeholder="lineage"/>
                                            <h5></h5>
                                            <input type="text" placeholder="sub-lineage"/>
                                            <h5></h5>
                                            <input type="text" placeholder="date of birth"/>
                                            <h5></h5>
                                        </div>
                                        <div className="col-md-6">
                                            <input type="text" placeholder="email address"/>
                                            <h5></h5>
                                            <input type="text" placeholder="fathers name"/>
                                            <h5></h5>
                                            <input type="text" placeholder="grand fathers name"/>
                                            <h5></h5>
                                            <input type="file" name="pic" accept="image/*"/>
                                            <h5></h5>
                                            <input type="file" name="pic" accept="image/*"/>
                                        </div>
                                        <div className="col-md-12">
                                            <input type="submit" value="send"/>
                                        </div>
                                    </form>
                                </div>
                               
                                <div class="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}
export default Add;




