import React from 'react';
import Tree from 'react-d3-tree';
import  axios from 'axios';
 
class MyComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state={myTreeData:[
    {
      name: 'Top Level',
      children: [
        {
          name: 'Level 2: A',
          children:[
            {
              name: 'level 2 chil- d name',
              parent: 'level 2'
            }
          ]
        },
        {
          name: 'Level 2: B',
        },
      ],
    },
  ],
  tr:[],
}

}

  componentDidMount() {
    
    	axios.post('http://localhost:3001/main/chart') // fetch from Express.js server
		.then(response =>{
      
      this.setState({tr:response.data});
      var l=response.data;
   
      function convert(array){
       var map = {}
       for(var i = 0; i < array.length; i++){
           var obj = array[i]
           if(!(obj.id in map)){
               map[obj.id] = obj
               map[obj.id].children = []
           }
   
           if(typeof map[obj.id].fname === 'undefined'){
               map[obj.id].id = obj.id
               map[obj.id].name = obj.name
               map[obj.id].attr = obj.attr
               map[obj.id].parent= obj.parent
           }
   
           var parent = obj.parent || '-';
           if(!(parent in map)){
               map[parent] = {}
               map[parent].children = []
           }
   
           map[parent].children.push(map[obj.id])
       }
       return map['-']
   }
   var res=convert(l);
           console.log(res) ;
            
           this.setState({myTreeData:[res]});
		})
	 }
  render() {
  
    return (
      <div className="container">
      { 
          this.state.tr.length > 0 ? this.state.tr.map(function(data) {
          return (
            <div>
              <div>
         
            <p>{data.pname}</p>
              </div>
            </div>);
        }): null
      }  
      <div id="treeWrapper" style={{width: '80em', height: '40em'}}>
      
        <Tree data={this.state.myTreeData} orientation="vertical" translate={{x:564 ,y:14 }} />
        
      </div>
      </div>
    );
  }
}
// module.exports = Dimensions()(TreeView);
export default MyComponent;


