import React, { Component } from "react";

class HeaderAdmin extends Component {
    render() {
        return (
            <div className="header">
                <div className="container">
                    <div className="header-top">
                        <div className="logo">
                            <a href="">Pushkarna <span>Ancestry Admin....</span></a>
                        </div>
                        <div className="top-menu">
                            <span className="menu"><img src="images/nav.png" alt=""/> </span>
                            <ul>
                                <li><a eventKey={1} componentClass="Home" href="/" to="/" href="/" className="active">home</a></li>
                                <li><a eventKey={2} componentClass="About" href="/about" to="/about" href="/about">about</a></li>
                                <li><a eventKey={3} componentClass="Culture" href="/culture" to="/culture" href="/culture">culture</a></li>
                                {/* <li><a eventKey={4} componentClass={Link} href="/surnames" to="/surnames" href="/surnames">surnames</a></li> */}
                                <li><a eventKey={4} componentClass="Ancestors" href="/ancestors" to="/ancestors" href="/ancestors">ancestors</a></li>
                                <li><a eventKey={5} componentClass="Add" href="/add" to="/add" href="/add">add</a></li> 
                                <li><a eventKey={6} componentClass="Contact" href="/contact" to="/contact" href="/contact">contact</a></li>
                                <li><a eventKey={7} componentClass="Register" to="/register" href="/register">signup </a></li>
                                <li><a eventKey={8} componentClass="Admin" href="/admin" to="/admin" href="/admin">admin </a></li>
                            </ul>
                        </div>
                        <div className="clerfix"></div>
                    </div>
                </div>
            </div>
        );
    }
}
export default HeaderAdmin;




