import React, { Component } from "react";
import "../Header.css";
class Potaedar extends Component {
    render() {
        return (
            <div>
                <div class="header-banner">
                    <div class="container">
                        <h3>पोतेदार </h3>
                    </div>
		        </div>
				<div class="content">
				    <div class="contact">
                        <div class="container">
                        
                            <div class="col-md-12">
                              
                               <img src="images/caste/ph/potaedar.JPG" class="img-responsive aboutimg" alt="/"/>
                              
                                <div class="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}
export default Potaedar;




