import React, { Component } from "react";
import  axios from 'axios';
import "../Header.css";
class Contact extends Component {

    handleSubmit(e){
        e.preventDefault();
        const name = document.getElementById('name').value;
        const email = document.getElementById('email').value;
        const message = document.getElementById('message').value;
        axios({
            method: "POST", 
            url:"http://localhost:8084/main/send", 
            data: {
                name: name,   
                email: email,  
                message: message
            }
        }).then((response)=>{
            if (response.data.msg === 'success'){
                alert("Message Sent."); 
                this.resetForm()
            }else if(response.data.msg === 'fail'){
                alert("Message failed to send.")
            }
        })
    }
    render() {
        return (
            <div>
                <div class="header-banner">
                    <div class="container">
                        <h3>संपर्क</h3>
                    </div>
		        </div>
				<div class="content">
				    <div class="contact">
                        <div class="container">
                            <div class="google-map">
                            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d894.3212266177649!2d73.01366852917741!3d26.284862401401963!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zMjbCsDE3JzA1LjUiTiA3M8KwMDAnNTEuMiJF!5e0!3m2!1sen!2sin!4v1537779378726" width="600" height="450" frameborder="0"  allowfullscreen></iframe>
                            </div>
                            <div class="contact-grids">
                                <div class="col-md-6 contact-left contactadd">
                                    <address>
                                        <p>Mr. Sada Shivan Vyas<br/>
                                        S/O Dr. J. M. Vyas</p>
                                        <p>Behind SBI Bank</p>
                                        <p>Outside Jalori Gate</p>
                                        <p>Jodhpur, Rajasthan</p>
                                        <p>Mobile: +91 9414283349 </p>
                                        <p>Telephone: +91 291 2626439 </p>
                                        <p>E-mail: s.s.vyas16@gmail.com</p>
							        </address>
					            </div>
                                <div class="col-md-6 contact-right">
                                    <form id="contact-form" onSubmit={this.handleSubmit.bind(this)} className="contactadd">
                                        <h5>name</h5>
                                        <input type="text" name="name" id="name"/>
                                        <h5>emailaddress</h5>
                                        <input type="text" name="email" id="email"/>
                                        <h5>message</h5>
                                        <textarea name="message" id="message"></textarea>
                                        <input type="submit" value="send"/>
                                    </form>
                                </div>
                                <div class="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}
export default Contact;




