import React, { Component } from "react";
import Axios from "axios";
class Add extends Component {
   constructor(props)
   {
       super(props);
       this.state={
           name:'',
           surname:'',
           dob:'',

       }
       this.handlerSubmit=this.handlerSubmit.bind(this);
       this.handler=this.handlerSubmit.bind(this);
   }
  handlerChange=event=>{
      this.setState({
          [event.target.id]:event.target.value
      })

  }
  
   handlerSubmit=event=>{
    event.preventDefault();
Axios.post('http://localhost:3001/main/chart',{
    name:this.state.name,
    surname:this.state.surname,
    dob:this.state.dob,
}).then(function(response) {
    this.props.history.push("/chart");
  });
   }
    render() {
        return (
            <div>
                <div class="header-banner">
                    <div class="container">
                        <h3>Search Your Ancestors</h3>
                    </div>
		        </div>
				<div class="content">
				    <div class="contact">
                        <div class="container">
                            <div class="contact-grids">
                                <div class="col-md-10 col-md-offset-1 contact-right">
                                    <form onSubmit={this.handlerSubmit}>
                                        <div class="col-md-4">
                                            <input type="text" placeholder="name" name="name"/>
                                            <h5></h5>
                                            <input type="text" placeholder="surname" name="surname"/>
                                            <h5></h5>
                                            <input type="date" name="dob"/>
                                            <h5></h5>
                                            <input type="submit" value="send"/>
                                        </div>
                                        </form>
                                        <div className="col-md-4">
                                            <input type="text" placeholder="name"/>
                                            <h5></h5>
                                            <input type="text" placeholder="surname"/>
                                            <h5></h5>
                                            <input type="text" placeholder="fathers name"/>
                                            <h5></h5>
                                            <input type="text" placeholder="grand fathers name"/>
                                            <h5></h5>
                                            <input type="submit" value="send"/>
                                        </div>
                                        <div className="col-md-4">
                                            <input type="text" placeholder="name"/>
                                            <h5></h5>
                                            <input type="text" placeholder="surname"/>
                                            <h5></h5>
                                            <input type="text" placeholder="lineage"/>
                                            <h5></h5>
                                            <input type="text" placeholder="sub-lineage"/>
                                            <h5></h5>
                                            <input type="submit" value="send"/>
                                        </div>
                                   
                                </div>
                               
                                <div class="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}
export default Add;




